 .wptravel-block-trip-fact .tour-info .tour-info-column {
    width: 100%;
    float: left;
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
    gap: 1em;
}

.wptravel-block-trip-fact .tour-info .tour-info-column .tour-info-item { 
    font-style: normal;
}